html, body {
    font-family: sans-serif;
    height: 100% !important;
    margin: 0 !important;
    padding: 0;
    font-size: 100%;
    border: 0;
    outline: 0;
    width: 100% !important;
}


@font-face {
  font-family: 'ocra';
  src: url('./fonts/OCRA.ttf') format("opentype");
}


.App{
  height: 100% !important;
  margin: 0 auto -20px;
}

nav{
  padding-top: 30px;
}

ul{
    padding-left: 0;
}

#company-name{
  width: 200px;
  background: white;
  font-weight: 1000;
  font-size: 18px;
}

ul li{
  display: inline-block;
  padding: 10px 30px;
  background: #00DCFF;
  width: 80px;
  text-align: center;
  border-radius: 50px;
  font-weight: bold;
  cursor: pointer;
}

h1{
  margin-top: 200px;
  font-size: 50px;
  font-weight: 100;
  font-family: 'ocra', sans-serif;
  color: #707070;
}

#front-page-info{
  font-size: 35px;
  font-weight: 100;
  margin-left: 30px;
  width: 810px;
}


/*--------responsive--------*/
@media only screen and (min-width: 1000px){
  header{
    height: 100% !important;
    height: 100% !important;
    padding: 0 5%;
    background-repeat: no-repeat;
    background-image: url('image/main-fig.png');
    background-size: contain;
    background-position: top -250px right -300px;
  }
}



